import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Tab extends Component {
    static propTypes = {
        activeTab: PropTypes.string.isRequired,
        label: PropTypes.string.isRequired,
        onClick: PropTypes.func.isRequired,
    };

    onClick = () => {
        const { label, onClick } = this.props;
        onClick(label);
    }

    render() {
        const {
            onClick,
            props: {
                activeTab,
                label,
            },
        } = this;

        let classNamess = 'nav-link';

        if (activeTab === label) {
            classNamess += 'nav-link active';
        }

        return (
            <li
                className="nav-item"
                onClick={onClick}
            >
                <p className={classNamess}>{label}</p>

            </li>
        );
    }
}

export default Tab;