import axios from 'axios'
import React, { Component } from 'react'

export default class MealForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        }
    }

    componentDidMount() {
        axios.get('/api/plans/6066360c920a2e311c95ee92').then((res) => {
            this.setState({ data: res.data })
        }).catch((err) => {
            console.error(err)
        })
    }
    multiplier = (name, base) => {
        const { data } = this.state;
        let unit2 = data.twoPlan.charAt(data.twoPlan.length - 1);
        let twosubs = parseInt(data.twoPlan, 10);
        let unit15 = data.fifteenPlan.charAt(data.fifteenPlan.length - 1);
        let fifteensubs = parseInt(data.fifteenPlan, 10);
        let unit30 = data.thirtyPlan.charAt(data.thirtyPlan.length - 1);
        let thirtysubs = parseInt(data.thirtyPlan, 10);
        let prod = "";
        if (name === "base_2price") {
            if (unit2 === '$') {
                prod = parseInt(base, 10) + parseFloat(twosubs)
                this.setState({
                    twoPlan: {
                        base_2price: twosubs,
                        customer_2price: prod
                    }
                })
            }
            else {
                prod = parseInt(base, 10) + (twosubs * 0.01 * base)
                this.setState({
                    twoPlan: {
                        base_2price: twosubs,
                        customer_2price: prod
                    }
                })
            }

            this.setState({ customer_2price: prod })
        }
        else if (name === "base_15price") {
            if (unit15 === '$') {
                prod = parseInt(base, 10) + parseFloat(fifteensubs)
                this.setState({
                    fifteenPlan: {
                        base_15price: fifteensubs,
                        customer_15price: prod
                    }
                })
            }
            else {
                prod = parseInt(base, 10) + (fifteensubs * base * 0.01)
                this.setState({
                    fifteenPlan: {
                        base_15price: fifteensubs,
                        customer_15price: prod
                    }
                })
            }
            this.setState({ customer_15price: prod })
        } else {
            if (unit30 === '$') {
                prod = parseInt(base, 10) + parseFloat(thirtysubs)
                this.setState({
                    thirtyPlan: {
                        base_30price: thirtysubs,
                        customer_30price: prod
                    }
                })
            }
            else {
                prod = parseInt(base, 10) + (thirtysubs * base * 0.01)
                this.setState({
                    thirtyPlan: {
                        base_30price: thirtysubs,
                        customer_30price: prod
                    }
                })
            }

            this.setState({ customer_30price: prod })
        }
    }
    onChangeHandler = (e) => {
        const { name, value } = e.target;
        this.multiplier(name, value)
        this.setState({ name: value })
    }
    setData = () => {
        let { twoPlan, fifteenPlan, thirtyPlan } = this.state
        let newPlan = {
            twoPlan: twoPlan,
            fifteenPlan: fifteenPlan,
            thirtyPlan: thirtyPlan
        }
        this.props.setPlan(newPlan)
    }
    render() {
        return (
            <>
                <div className="d-flex align-items-center justify-content-between mg-t-25 mg-b-25">
                    <h6 className="tx-12  mg-b-0">Bank Details</h6>
                    <button className="btn btn-link" type="button" onClick={this.setData} >Save</button>
                </div>
                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">2 Day Meal Plan</h6>
                </div>

                <div className="row row-sm mx-1">
                    <div className="col-6 col-sm-6 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                name="base_2price"
                                value={this.state.base_2price}
                                onChange={this.onChangeHandler}
                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-6 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                readOnly
                                name="customer_2price"
                                value={this.state.customer_2price || ""}
                            />
                        </p>
                    </div>
                </div>


                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">15 Day Meal Plan</h6>
                </div>

                <div className="row row-sm mx-1">
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="base_15price"
                                value={this.state.base_15price}
                            />
                        </p>
                    </div>
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className=" mg-b-0">
                            <input
                                readOnly
                                name="customer_15price"
                                value={this.state.customer_15price || ""}
                            />
                        </p>
                    </div>
                </div>

                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">30 Day Meal Plan</h6>
                </div>
                <div className="row row-sm mx-1">
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="base_30price"
                                value={this.state.base_30price}
                            />
                        </p>
                    </div>
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                readOnly
                                name="customer_30price"
                                value={this.state.customer_30price || ""}
                            />
                        </p>
                    </div>
                </div>


            </>
        )
    }
}
