import React, { useState, Fragment } from "react";
import "bootstrap/dist/css/bootstrap.css";
import { imageConverter } from "../../helper/imageConverter";

const DynamicImages = (props) => {
    const [inputFields, setInputFields] = useState([{ documents: {} }]);

    const handleAddFields = () => {
        const values = [...inputFields];
        values.push({ documents: {} });
        setInputFields(values);
    };

    const handleRemoveFields = index => {
        const values = [...inputFields];
        values.splice(index, 1);
        setInputFields(values);
    };

    const handleInputChange = (index, event) => {
        const values = [...inputFields];
        imageConverter(event.target.files[0], event.target.name).then((res) => {
            values[index].documents = res
            setInputFields(values);
        })
            .catch((err) => {
                console.error(err)
            })
        let allImages = []
        allImages = [...props.documents]
        let result = inputFields.map(({ documents }) => documents)
        let docs = result.concat(allImages)
        props.getImages(docs)

    };

    return (
        <>
            <div className="row row-sm mg-t-10 px-2 mg-b-10">
                {inputFields.map((inputField, index) => (
                    <Fragment key={`${inputField}~${index}`}>
                        <div className="form-group col-sm-10">
                            <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                {'image '.concat((index + 1).toString())}
                            </label>
                            <div className="custom-file px-2">
                                <input
                                    type="file"
                                    className="custom-file-input"
                                    accept=".png, .jpg, .webp, .gif"
                                    id="firstName"
                                    name={"image".concat((index + 1).toString())}
                                    value={inputField.image}
                                    onChange={event => handleInputChange(index, event)}
                                />
                                <label className="custom-file-label" htmlFor="customFile">
                                    Choose File
                                </label>
                            </div>

                        </div>
                        <div className="form-group col-sm-2 mt-2">
                            <button
                                className="btn btn-link"
                                style={{ fontSize: 26, textAlign: 'center' }}
                                type="button"
                                onClick={() => handleRemoveFields(index)}>-</button>
                            <button
                                className="btn btn-link"
                                style={{ fontSize: 26, textAlign: 'center' }}
                                type="button"
                                onClick={() => handleAddFields()}
                            > +</button>
                        </div>
                    </Fragment>
                ))}
            </div>
        </>
    );
};

export default DynamicImages;