import React, { Component } from "react";
import axios from "axios";
import { Trash2, } from "react-feather";
import { Modal, Button, Toast } from 'react-bootstrap'
import { Convert } from 'mongo-image-converter';

export default class Cuisine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpenChange: false,
            cuisineName: "",
            isLoading: false,
            image: null,
            cuisines: [],
            imgLabel: "Choose File",
            showDeleteModal: false,
            currentindex: '',
            showToast: false
        };

    }

    componentDidMount() {
        axios.get('/api/cuisine').then((res) => {
            this.setState({ cuisines: res.data })
        })
            .catch((err) => {
                console.error(err)
            })
    }

    convertImage = async (event) => {
        event.preventDefault()
        const image = this.state.image;
        const cuisineName = this.state.cuisineName;
        try {
            const convertedImage = await Convert(image)
            if (convertedImage) {
                const cuisine = {
                    image: convertedImage,
                    cuisineName: cuisineName
                };
                axios.post('/api/cuisine/', cuisine).then((res) => {
                    console.log(res.data)
                    this.setState({ showToast: true })
                }).catch((err) => {
                    console.log(err)
                })
            } else {
                console.log('The file is not in format of image/jpeg or image/png')
            }
        }
        catch (error) {
            console.error(error.message)
        }
    }
    onChangeHandler = (e) => {
        const value = e.target.value;
        this.setState({
            [e.target.name]: value,
        });
    }
    onChangeFile = (e) => {
        this.setState({ image: e.target.files[0], imgLabel: e.target.files[0].name });
    }
    handleClose = () => {
        this.setState({ showDeleteModal: false })
    }
    handleDelete = () => {
        axios.delete('/api/cuisine/' + this.state.currentindex).then((res) => {
            console.log('deleted')
        }).catch((err) => {
            console.error(err)
        })
        window.location.reload()
    }

    render() {
        const { isLoading, cuisines, showDeleteModal, showToast } = this.state
        return (
            <div className="contact-content p-3 ml-n5">
                <div
                    data-label="Manage Cuisine "
                    className="df-example demo-forms mb-4"
                >
                    <form onSubmit={this.convertImage}>
                        <div className="d-flex justify-content-end ">
                            <div className="px-2">
                                <button
                                    className="btn btn-sm  btn-icon btn-white px-4 mx-1"
                                >Cancel</button>
                                <button
                                    className="btn btn-sm  btn-icon btn-primary px-4 mx-1"
                                    onPress={() => this.convertImage}
                                >Save</button>
                            </div>
                        </div>
                        <div className="p-3">
                            <div className="row mt-2">
                                <table className='table table-hover'>
                                    <thead style={{ backgroundColor: "#f5f6fa", fontWeight: "bold" }} >
                                        <tr>
                                            <th style={{ width: '40%' }} >Cuisine Name</th>
                                            <th style={{ width: '40%' }} >Image</th>
                                            <th style={{ width: '20%' }} >Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            !isLoading && cuisines.map((data, key) => {
                                                return (
                                                    <tr>
                                                        <td style={{ width: '40%', borderRight: '1px solid #ddd ', }}>{data.cuisineName}</td>
                                                        <td style={{ width: '60%' }}>
                                                            <img src={data.image} height={50} width={50} alt={data.cuisineName} />
                                                        </td>
                                                        <td style={{ width: '20%' }}>
                                                            <Trash2 onClick={() => { this.setState({ showDeleteModal: true, currentindex: data._id }) }} size={18} />
                                                        </td>
                                                    </tr>
                                                )
                                            })
                                        }
                                    </tbody>
                                </table>

                            </div>


                            <div className="row mt-2">
                                <label className="labels">Cuisine Name</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Cuisine Name"
                                    name="cuisineName"
                                    onChange={this.onChangeHandler}
                                />
                            </div>
                            <div className="row mt-2">
                                <label className="labels">Image</label>
                                <div className="custom-file px-2">
                                    <input
                                        type="file"
                                        className="custom-file-input"
                                        accept=".png, .jpg, .webp, .gif"
                                        name="profile_image"
                                        onChange={this.onChangeFile}
                                    />
                                    <label className="custom-file-label" for="customFile">
                                        {this.state.imgLabel}
                                    </label>
                                </div>
                            </div>

                        </div>
                    </form>
                </div>
                <Modal
                    show={showDeleteModal}
                    onHide={this.handleClose}
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Modal title</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        Are you sure you want to delete? Action is irreversible!!!
        </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Close
          </Button>
                        <Button onClick={this.handleDelete} variant="danger">Delete</Button>
                    </Modal.Footer>
                </Modal>

                <Toast style={{ position: 'absolute', top: 0, right: 50, borderColor: '#44cf33' }} onClose={() => { this.setState({ showToast: false }); window.location.reload() }} show={showToast} delay={2000} autohide>
                    <Toast.Body>
                        Cuisine Added Succesfully!!!
                    </Toast.Body>
                </Toast>
            </div>
        );
    }
}
