import React, { useState } from "react";
import { Plus, Trash2 } from "react-feather";

function DynamicAddOn({ sendDataToParent }) {
    const [inputList, setInputList] = useState([{ add_on: "", add_on_price: "" }]);

    // handle input change
    const handleInputChange = (index, event) => {
        const { name, value } = event.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
        sendDataToParent(list)
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { add_on: "", add_on_price: "" }]);
    };

    return (
        <>
            {inputList.map((x, i) => {
                return (
                    <div key={i}>
                        <div key={i} className="row row-sm px-2">
                            <div className="col-sm-5 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Add On
                                </label>
                                <p className="mg-b-0">
                                    <input
                                        name="add_on"
                                        className="form-control"
                                        value={inputList.add_on}
                                        onChange={event => handleInputChange(i, event)}
                                    />
                                </p>
                            </div>
                            <div className="col-sm-5 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    price
                                </label>
                                <p className="mg-b-0">
                                    <input
                                        value={inputList.add_on_price}
                                        className="form-control"
                                        onChange={event => handleInputChange(i, event)}
                                        name="add_on_price"

                                    />
                                </p>
                            </div>
                            <div className="col-sm-2 mg-t-20 mg-sm-t-40 px-3 center">
                                {inputList.length !== 1 &&
                                    <button
                                        className="btn btn-sm btn-white mg-t-20 mx-2"
                                        onClick={() => handleRemoveClick(i)}>
                                        <Trash2 />
                                    </button>
                                }
                                {inputList.length - 1 === i && <button className="btn btn-sm btn-whitex mg-t-20" onClick={handleAddClick}>
                                    <Plus />
                                </button>}
                            </div>
                        </div>
                    </div>
                );
            })}

        </>
    );
}

export default DynamicAddOn;