import React, { Component } from 'react'

export default class BankDetailsForm extends Component {
    constructor(props) {
        super(props);
        this.state={}
    }
    
    onChangeHandler = (e) => {
        const {value } = e.target;
        this.setState({ [e.target.name]: value })
    }
    setData = () => {
        let bank_info  = {...this.state}
        this.props.setBank(bank_info)
    }
    render() {
        return (
            <>
                <div className="d-flex align-items-center justify-content-between mg-t-25 mg-b-25">
                    <h6 className="tx-12  mg-b-0">Bank Details</h6>
                    <button className="btn btn-link" type="button" onClick={this.setData} >Save</button>
                </div>
                <div className="row row-sm">
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Account Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="account_name"
                                value={this.state.account_name || ""}
                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Account Number
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="account_number"
                                value={this.state.account_number}
                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Confirm Account Number
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="account_confirm"
                                value={this.state.account_confirm}
                            />
                        </p>
                    </div>
                </div>
                <div className="row row-sm">
                    <div className="col-sm-4 mg-sm-t-10">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Bank Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="bank_name"
                                value={this.state.bank_name}
                            />
                        </p>
                    </div>
                    <div className="col-sm-4 mg-sm-t-10">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Branch #
                    </label>
                        <p className=" mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="branch_number"
                                value={this.state.branch_number}
                            />
                        </p>
                    </div>
                    <div className="col-sm-4 mg-t-20 mg-sm-t-10">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Institution #
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="institution_number"
                                value={this.state.institution_number}
                            />
                        </p>
                    </div>
                </div>

                
            </>
        )
    }
}
