import React, { useState } from "react";
import { Trash2 } from "react-feather";
import { imageConverter } from "../../helper/imageConverter";
import DynamicAddOn from "./DynamicAddOn";

function DynamicMeal({ setMeal }) {
    const [inputList, setInputList] = useState([{}]);
    const [addOn, setaddOn] = useState(null)

    const sendDataToParent = index => {
        setaddOn(index)
    }

    // handle input change
    const handleInputChange = (index, event) => {
        const { name, value } = event.target;
        const list = [...inputList];
        if (event.target.type === "file") {
            imageConverter(event.target.files[0], event.target.name).then((res) => {
                list[index][name] = res.image
            }).catch((err) => {
                console.error(err)
            })
        } else {
            list[index][name] = value
        }
        setInputList(list);
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { type: "", time: "", name: "", image: "", day: "", add_on: "", add_on_price: "" }]);
    };

    const setData = () => {
        let meals = { ...inputList }
        let addOsn = { ...addOn }
        setMeal(meals, addOsn)
    }

    return (
        <>
            <div className="d-flex align-items-center justify-content-between mx-2">
                <h6 className="tx-12  mg-b-0">Foods</h6>
                <button className="btn btn-link" type="button" onClick={setData} >Save</button>
            </div>
            {inputList.map((x, i) => {
                return (
                    <div key={i}>
                        <div className="row px-1">
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Available as
                            </label>
                                <p className="mg-b-0">
                                    <select className="custom-select" name="slot" onChange={event => handleInputChange(i, event)} value={inputList.lunch}>
                                        <option name="slot" value="Lunch">Lunch</option>
                                        <option name="slot" value="Dinner">Dinner</option>
                                    </select>
                                </p>

                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Meal type
                            </label>
                                <p className="mg-b-0">
                                    <select className="custom-select" name="type" onChange={event => handleInputChange(i, event)} value={inputList.type} >
                                        <option name="type" value="Veg">Veg</option>
                                        <option name="type" value="non-veg">Non-Veg</option>
                                    </select>
                                </p>
                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Days
                            </label>
                                <p className="mg-b-0">
                                    <select className="custom-select" name="day" onChange={event => handleInputChange(i, event)} value={inputList.day} >
                                        <option name="day" value="Monday">Monday</option>
                                        <option name="day" value="Tuesday">Tuesday</option>
                                        <option name="day" value="Wednesday">Wednesday</option>
                                        <option name="day" value="Thursday">Thursday</option>
                                        <option name="day" value="Friday">Friday</option>
                                        <option name="day" value="Saturday">Saturday</option>
                                        <option name="day" value="Sunday">Sunday</option>
                                    </select>
                                </p>
                            </div>
                        </div>

                        <div className="row row-sm px-2">
                            <div className="col-6 col-sm-4 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Meal Name
                                </label>
                                <p className="mg-b-0">
                                    <input
                                        name="meal_name"
                                        className="form-control"
                                        onChange={event => handleInputChange(i, event)}
                                        value={inputList.meal_name}
                                    />
                                </p>
                            </div>
                            <div className="col-6 col-sm-8 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Image
                                </label>
                                <div className="custom-file px-2">
                                    <input
                                        type="file"
                                        className="custom-file-input"
                                        id="customFile"
                                        name="image"
                                        value={inputList.image}
                                        onChange={event => handleInputChange(i, event)}
                                    />
                                    <label className="custom-file-label" htmlFor="customFile">
                                        Choose file
                                    </label>
                                </div>
                            </div>
                        </div>

                        <div className="row row-sm px-2">
                            <div className="col-6 col-sm-12 mg-sm-t-10">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Description
                                </label>
                                <p className="mg-b-0">
                                    <textarea
                                        className="form-control"
                                        name="description"
                                        value={inputList.description}
                                        onChange={event => handleInputChange(i, event)}
                                    />
                                </p>
                            </div>
                        </div>
                        <DynamicAddOn sendDataToParent={sendDataToParent} />
                        <div className="row row-sm px-2 mg-b-10">
                            <div className="col-6 col-sm-6 mg-t-20 mg-sm-t-30 px-3">
                                {inputList.length - 1 === i && <button
                                    className="btn btn-sm btn-primary mg-sm-t-10"
                                    onClick={handleAddClick}>
                                    Add More Meal
                                    </button>}
                            </div>
                            <div className="col-6 col-sm-6 mg-t-20 mg-sm-t-30 px-3">
                                {inputList.length !== 1 &&
                                    <div className="d-inline-block mg-sm-t-10  bd bd-danger rounded" >
                                        <button
                                            className="btn btn-sm btn-white mg-sm-t-10 mx-3"
                                            onClick={() => handleRemoveClick(i)}>
                                            <Trash2 />
                                        </button>
                                    </div>}
                            </div>
                        </div>


                    </div>
                );
            })}

        </>
    );
}

export default DynamicMeal;