import React, { Component } from "react";
import "jquery";
import "../../assets/css/dashforge.css";
// import { addRestaurant } from "../../controllers/RestaurantController";
import DynamicMeal from "./DynamicMeal";
// import DynamicImages from "./DynamicImages";
// import $ from 'jquery'
import RestaurantDetailForm from "./RestaurantDetailForm";
import MealForm from "./MealForm";
import BankDetailsForm from "./BankDetailsForm";
import ImageTabDetails from "./ImageTabDetails";
import axios from "axios";

export default class AddUserForm extends Component {
  constructor(props) {
    super(props);
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.state = {

    };
  }
  onChangeHandler(e) {
    const value = e.target.value;
    this.setState({
      [e.target.name]: value,
    });
  }
  onSubmit(e) {
    e.preventDefault();
    const data = this.state;
    console.log(data);
    axios.post('/api/newrest', data).then((res) => {
      console.log(res.data)
    }).catch((err) => {
      console.error(err)
    })
    window.location.href = "/restaurant-dashboard";
  }
  detailsFilled = (vals) => {
    this.setState({ ...vals })
  }
  getImages = (data) => {
    this.setState({ documents: data.documents })
  }
  setMeal = (meal, addOn) => {
    let allMeals = []
    let ml = {
      meal: meal,
      add_on: addOn
    }
    allMeals.push(ml)
    this.setState({ meal: allMeals })
  }
  setPlan = (data) => {
    console.log(data);
    this.setState({
      plan: data
    })

  }
  setBank = (data) => {
    this.setState({
      bank_info: data
    })
  }

  changeTab = (e) => {
    var i, tabcontent, tablinks;
    var tabName = e.target.value;
    tabcontent = document.getElementsByClassName("resttab");
    for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].style.display = "none";
    }
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    document.getElementById(tabName).style.display = "block";
    e.currentTarget.className += " active";
  };


  render() {
    return (
      <div className="contact-content" style={{ height: 640, marginBottom: 100, overflowY: 'scroll' }} >
        <div
          className="tab-content"
          style={{ position: "absolute", left: 0, width: "80%" }}
        >
          <div id="contactInformation" className=" pd-20 pd-xl-25">
            <div
              className="row row-sm"
              style={{ backgroundColor: "#fff", marginTop: -10 }}
            >
              <div className="col-sm mg-t-10 mg-sm-t-10 mg-b-10">
                <h5 className="tx mg-t-5">Add Restaurant</h5>
              </div>
              <div className="col-sm mg-t-10 mg-sm-t-10 mg-b-10">
                {" "}
                <div className="d-flex px-5 justify-content-end">
                  <button
                    onClick={() => {
                      window.location.href = "/restaurant-dashboard";
                    }}
                    className="btn btn-sm btn-white mr-2"
                    style={{ zIndex: 1000 }}
                  >
                    Cancel
                    </button>
                  <button
                    onClick={this.onSubmit}
                    className="btn btn-sm  btn-icon btn-primary px-4 mx-1"
                    style={{ zIndex: 1000 }}
                  >
                    Save
                    </button>
                </div>
              </div>
            </div>
            <div className="row row-sm tab">
              <button
                className="tablinks"
                onClick={this.changeTab}
                name="Details"
                value="Details"
                id="btn1"
              >
                Details
                </button>
              <button
                className="tablinks"
                onClick={this.changeTab}
                name="Images"
                value="Images"
                id="btn2"
              >
                Images
                </button>
              <button
                className="tablinks"
                onClick={this.changeTab}
                name="Meals"
                value="Meals"
                id="btn3"
              >
                {" "}
                  Meals
                </button>
              <button
                className="tablinks"
                onClick={this.changeTab}
                name="Plans"
                value="Plans"
                id="btn4"
              >
                {" "}
                  Meal Plan
                </button>
              <button
                className="tablinks"
                onClick={this.changeTab}
                name="Bank"
                value="Bank"
                id="btn5"
              >
                {" "}
                  Bank Details
                </button>
            </div>

            <form onSubmit={this.onSubmit}>

              <div id="Details" className="resttab">
                <RestaurantDetailForm {...this.state} detailsFilled={this.detailsFilled} />
              </div>
              {/* Details tab */}
              <div id="Images" className="resttab">

                <ImageTabDetails {...this.state} getImages={this.getImages} />
              </div>

              {/* Image Tab */}
              <div id="Meals" className="resttab">

                <DynamicMeal setMeal={this.setMeal} />

              </div>

              {/* Meals Tab */}
              <div id="Plans" className="resttab">
                <MealForm setPlan={this.setPlan} />
              </div>

              {/* Plan Tab */}
              <div id="Bank" className="resttab">

                <BankDetailsForm setBank={this.setBank} />
              </div>
              {/* Bank Details */}
            </form>
          </div>
        </div>
      </div>

    );
  }
}
