import Tabs from "../../helper/Tabs";
import React, { Component } from "react";
import SideNav from "../components/SideNav";
import TopNav from "../components/TopNav";
import RestaurantDetailForm from "../components/RestaurantDetailForm";
import ImageTabDetails from "../components/ImageTabDetails";
import MealForm from "../components/MealForm";
import BankDetailsForm from "../components/BankDetailsForm";
import DynamicMeal from '../components/DynamicMeal'

export default class ViewRestaurant extends Component {
    constructor(props) {
        super(props);
        this.state = {
            header: ["Details", "Images", "Meals", "Meal Plan", "Bank Details"]
        }
    }
    components = [<RestaurantDetailForm/>,<ImageTabDetails/>,<DynamicMeal/>  ,<MealForm/>,<BankDetailsForm/> ]

    render() {
        const { header } = this.state
        return (
            <>
                <TopNav />
                <div className="contact-wrapper">
                    <SideNav />
                    <div className="contact-content" style={{ height: 640, marginBottom: 100, overflowY: 'scroll' }} >
                        <div
                            className="tab-content"
                            style={{ position: "absolute", left: 0, width: "80%" }}
                        >
                            <div id="contactInformation" className=" pd-20 pd-xl-25">
                                <Tabs>
                                    {
                                        header.map((data, i) => {
                                            return (
                                                <div className="tab-pane fade show active" label={data}>
                                                    {this.components[i]}
                                                </div>
                                            )
                                        })
                                    }
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}
