import axios from 'axios';
import React, { Component } from 'react'

export default class RestaurantDetailForm extends Component {
    constructor(props) {
        super(props);
        this.onChangeHandler = this.onChangeHandler.bind(this)
        this.state = {
            ...this.props,
            cuisines:[]
        }
    }
    componentDidMount() {
        axios.get('/api/cuisine').then(res => {
            this.setState({cuisines:res.data})
        }).catch(err => {
            console.error(err)
        })
    }
    onChangeHandler(e) {
        const value = e.target.value;
        this.setState({
            [e.target.name]: value,
        });
    }
    setData = () => {
        this.props.detailsFilled({...this.state})
    }

    render() {
        const cuisines=this.state.cuisines
        return (
            <>
                <div className="d-flex align-items-center justify-content-between mx-2">
                    <h6 className="tx-12  mg-b-0">Restaurant Details</h6>
                    <button className="btn btn-link" type="button" onClick={this.setData}>Save</button>
                </div>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Restaurant Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="restaurant_name"
                                value={this.state.restaurant_name}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Phone Number
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="phone"
                                value={this.state.phone}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Email Address
                    </label>
                        <p className=" mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="email"
                                value={this.state.email}

                            />
                        </p>
                    </div>


                </div>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Owner Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="owner_name"
                                value={this.state.owner_name}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Street
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="locality"
                                value={this.state.locality}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            City
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="city"
                                value={this.state.city}

                            />
                        </p>
                    </div>
                </div>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            State
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="state"
                                value={this.state.state}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Country
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="country"
                                value={this.state.country}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Postal Code
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="postal_code"
                                value={this.state.postal_code}

                            />
                        </p>
                    </div>
    
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Cuisine Type
                    </label>
                        <select
                            style={{ width: 176, height: 30,padding:0 }}
                            className="custom-select"
                            value={this.state.cuisine_type}
                            name="cuisine_type"
                            onChange={this.onChangeHandler}
                        >
                            {
                                cuisines && cuisines.map((data, key) => {
                                    return (
                                        <option key={key} className="mg-b-0" name="cuisine_type" value={data.cuisineName }>{ data.cuisineName }</option>
                                    )
                                })
                            }
                        </select>
                    </div>
                  
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Commission
                    </label>
                        <p className="mg-b-0">
                            <input
                                onChange={this.onChangeHandler}
                                name="commision"
                                value={this.state.commission}

                            />
                        </p>
                    </div>

                    
                    <div className="col-6 col-sm-4 ">
                        <div className="col-6 col-sm-4">
                            <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10 ml-n3">
                                Status
                      </label>
                            <select
                                style={{ width: 176, height: 30, marginLeft: -15,padding:0}}
                                className="custom-select"
                                name="status"
                                onChange={this.onChangeHandler}
                            >
                                <option className="mg-b-0" name="status" value="Active">Active</option>
                                <option className="mg-b-0" name="status" value="Inactive">Inactive</option>
                                <option className="mg-b-0" name="status" value="Unapproved">Unapproved</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div className="row row-sm px-1  mg-t-20">
                    <div className="col-6 col-sm  mg-t-10">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            About
                    </label>
                        <p className="mg-b-0 px-1">
                            <textarea
                                className="col-sm mg-t-10"
                                onChange={this.onChangeHandler}
                                name="about"
                                value={this.state.about}
                                maxLength={400}
                            />
                        </p>
                    </div>
                </div>
                {/* row */}
            </>
        )
    }
}
